var echarts = require("../echarts");

var graphic = require("../util/graphic");

var _layout = require("../util/layout");

var getLayoutRect = _layout.getLayoutRect;
// Model
echarts.extendComponentModel({
  type: 'title',
  layoutMode: {
    type: 'box',
    ignoreSize: true
  },
  defaultOption: {
    // ä¸€çº§å±‚å 
    zlevel: 0,
    // äºŒçº§å±‚å 
    z: 6,
    show: true,
    text: '',
    // è¶…é“¾æŽ¥è·³è½¬
    // link: null,
    // ä»…æ”¯æŒself | blank
    target: 'blank',
    subtext: '',
    // è¶…é“¾æŽ¥è·³è½¬
    // sublink: null,
    // ä»…æ”¯æŒself | blank
    subtarget: 'blank',
    // 'center' Â¦ 'left' Â¦ 'right'
    // Â¦ {number}ï¼ˆxåæ ‡ï¼Œå•ä½pxï¼‰
    left: 0,
    // 'top' Â¦ 'bottom' Â¦ 'center'
    // Â¦ {number}ï¼ˆyåæ ‡ï¼Œå•ä½pxï¼‰
    top: 0,
    // æ°´å¹³å¯¹é½
    // 'auto' | 'left' | 'right' | 'center'
    // é»˜è®¤æ ¹æ® left çš„ä½ç½®åˆ¤æ–­æ˜¯å·¦å¯¹é½è¿˜æ˜¯å³å¯¹é½
    // textAlign: null
    //
    // åž‚ç›´å¯¹é½
    // 'auto' | 'top' | 'bottom' | 'middle'
    // é»˜è®¤æ ¹æ® top ä½ç½®åˆ¤æ–­æ˜¯ä¸Šå¯¹é½è¿˜æ˜¯ä¸‹å¯¹é½
    // textBaseline: null
    backgroundColor: 'rgba(0,0,0,0)',
    // æ ‡é¢˜è¾¹æ¡†é¢œè‰²
    borderColor: '#ccc',
    // æ ‡é¢˜è¾¹æ¡†çº¿å®½ï¼Œå•ä½pxï¼Œé»˜è®¤ä¸º0ï¼ˆæ— è¾¹æ¡†ï¼‰
    borderWidth: 0,
    // æ ‡é¢˜å†…è¾¹è·ï¼Œå•ä½pxï¼Œé»˜è®¤å„æ–¹å‘å†…è¾¹è·ä¸º5ï¼Œ
    // æŽ¥å—æ•°ç»„åˆ†åˆ«è®¾å®šä¸Šå³ä¸‹å·¦è¾¹è·ï¼ŒåŒcss
    padding: 5,
    // ä¸»å‰¯æ ‡é¢˜çºµå‘é—´éš”ï¼Œå•ä½pxï¼Œé»˜è®¤ä¸º10ï¼Œ
    itemGap: 10,
    textStyle: {
      fontSize: 18,
      fontWeight: 'bolder',
      color: '#333'
    },
    subtextStyle: {
      color: '#aaa'
    }
  }
}); // View

echarts.extendComponentView({
  type: 'title',
  render: function (titleModel, ecModel, api) {
    this.group.removeAll();

    if (!titleModel.get('show')) {
      return;
    }

    var group = this.group;
    var textStyleModel = titleModel.getModel('textStyle');
    var subtextStyleModel = titleModel.getModel('subtextStyle');
    var textAlign = titleModel.get('textAlign');
    var textBaseline = titleModel.get('textBaseline');
    var textEl = new graphic.Text({
      style: graphic.setTextStyle({}, textStyleModel, {
        text: titleModel.get('text'),
        textFill: textStyleModel.getTextColor()
      }, {
        disableBox: true
      }),
      z2: 10
    });
    var textRect = textEl.getBoundingRect();
    var subText = titleModel.get('subtext');
    var subTextEl = new graphic.Text({
      style: graphic.setTextStyle({}, subtextStyleModel, {
        text: subText,
        textFill: subtextStyleModel.getTextColor(),
        y: textRect.height + titleModel.get('itemGap'),
        textVerticalAlign: 'top'
      }, {
        disableBox: true
      }),
      z2: 10
    });
    var link = titleModel.get('link');
    var sublink = titleModel.get('sublink');
    textEl.silent = !link;
    subTextEl.silent = !sublink;

    if (link) {
      textEl.on('click', function () {
        window.open(link, '_' + titleModel.get('target'));
      });
    }

    if (sublink) {
      subTextEl.on('click', function () {
        window.open(sublink, '_' + titleModel.get('subtarget'));
      });
    }

    group.add(textEl);
    subText && group.add(subTextEl); // If no subText, but add subTextEl, there will be an empty line.

    var groupRect = group.getBoundingRect();
    var layoutOption = titleModel.getBoxLayoutParams();
    layoutOption.width = groupRect.width;
    layoutOption.height = groupRect.height;
    var layoutRect = getLayoutRect(layoutOption, {
      width: api.getWidth(),
      height: api.getHeight()
    }, titleModel.get('padding')); // Adjust text align based on position

    if (!textAlign) {
      // Align left if title is on the left. center and right is same
      textAlign = titleModel.get('left') || titleModel.get('right');

      if (textAlign === 'middle') {
        textAlign = 'center';
      } // Adjust layout by text align


      if (textAlign === 'right') {
        layoutRect.x += layoutRect.width;
      } else if (textAlign === 'center') {
        layoutRect.x += layoutRect.width / 2;
      }
    }

    if (!textBaseline) {
      textBaseline = titleModel.get('top') || titleModel.get('bottom');

      if (textBaseline === 'center') {
        textBaseline = 'middle';
      }

      if (textBaseline === 'bottom') {
        layoutRect.y += layoutRect.height;
      } else if (textBaseline === 'middle') {
        layoutRect.y += layoutRect.height / 2;
      }

      textBaseline = textBaseline || 'top';
    }

    group.attr('position', [layoutRect.x, layoutRect.y]);
    var alignStyle = {
      textAlign: textAlign,
      textVerticalAlign: textBaseline
    };
    textEl.setStyle(alignStyle);
    subTextEl.setStyle(alignStyle); // Render background
    // Get groupRect again because textAlign has been changed

    groupRect = group.getBoundingRect();
    var padding = layoutRect.margin;
    var style = titleModel.getItemStyle(['color', 'opacity']);
    style.fill = titleModel.get('backgroundColor');
    var rect = new graphic.Rect({
      shape: {
        x: groupRect.x - padding[3],
        y: groupRect.y - padding[0],
        width: groupRect.width + padding[1] + padding[3],
        height: groupRect.height + padding[0] + padding[2],
        r: titleModel.get('borderRadius')
      },
      style: style,
      silent: true
    });
    graphic.subPixelOptimizeRect(rect);
    group.add(rect);
  }
});